import { Component } from '@angular/core';

declare var meed;

@Component({
  selector: 'app-home',
  templateUrl: 'home.page.html',
  styleUrls: ['home.page.scss'],
})
export class HomePage {
  constructor() {}

  checkPermissions() {
    const permissions = ['camera', 'location'];
    meed.plugins.Socure.checkPermissions(
      permissions,
      (result) => {
        console.log(result);
      },
      (err) => {
        console.log(err);
      }
    );
  }

  scanLicense() {
    meed.plugins.Socure.scanLicense(
      (result) => {
        console.log(result);
      },
      (err) => {
        console.log(err);
      }
    );
  }

  scanPassport() {
    meed.plugins.Socure.scanPassport(
      (result) => {
        console.log(result);
      },
      (err) => {
        console.log(err);
      }
    );
  }

  scanSelfie() {
    meed.plugins.Socure.scanSelfie(
      (result) => {
        console.log(result);
      },
      (err) => {
        console.log(err);
      }
    );
  }

  openSetting() {
    meed.plugins.Socure.openSetting(
      (result) => {
        console.log(result);
      },
      (err) => {
        console.log(err);
      }
    );
  }
}
